.header {
    position   : relative;
    grid-column: 1 / 3;
    grid-row   : 1 / 2;
    display    : grid;
    gap        : 20px;
    min-height : 80px;

    background-color     : aquamarine;
    align-items          : center;
    grid-template-rows   : 1fr;
    grid-template-columns: 1.0fr 11.0fr;

}

.header>div {}


.logo__link {
    width       : 80px;
    justify-self: center;
    grid-column : 1 / 2;
    grid-row    : 1 / 2;

}

.header img {
    cursor   : pointer;
    max-width: 100%;
}

.header__container {}

.login__block {
    position        : absolute;
    top             : 50%;
    transform       : translate(0, -50%);
    right           : 20px;
    background-color: cadetblue;
    padding         : 10px;
    border-radius   : 7px;
}

.login__block>div {
    /*     display: flex;
    flex-direction: column;
    
    justify-content: center;
 */
    text-align: right;
}

.login__block>div>a {
    color  : white;
    display: inline-block;

    font-weight: 700;

}

.exit {
    color          : white;
    text-decoration: underline;
    cursor: pointer;
}